const inventorySlice = createSlice({
    name: 'inventory',
    initialState: {
        productList: initialState,
        disableList: [],
    },
    reducers: {
        setProductList: (state, action) => {
            state.productList = action.payload;
        },
        setDisableList: (state, action) => {
            state.disableList = action.payload;
        }
    }
});

export const { setProductList, setDisableList } = inventorySlice.actions;

export default inventorySlice.reducer;

